<template>
  <div>
    <div id="user-picker" ref="user-picker" class="schedulecc-no-card px-lg-3 mx-lg-2">
      <div class="align-items-center border-thin-top d-md-flex justify-content-between mt-4 pt-3 pb-md-2 pb-3 mb-md-1 mb-5">
        <!-- Time zone starts here -->
        <div class="mt-2">
          <h4 class="mb-1 font-weight-bold">Select Date and Time</h4>
          <p class="text-muted small mb-0 flex-wrap">
            <template v-if="eventData && eventData.location">Location :- </template>
            <span class="text-body">
              <b>{{ eventData.location }}</b>
              <template v-if="eventData && eventData.location_info && JSON.parse(eventData.location_info).location_address && JSON.parse(eventData.location_info).location_address != 'invitee_call'">
                -{{ JSON.parse(eventData.location_info).location_address }}
              </template>
              <template v-if="eventData && eventData.location_info && JSON.parse(eventData.location_info).location_more_info">, {{ JSON.parse(eventData.location_info).location_more_info }}</template>
            </span>
          </p>
        </div>
        <div class="w-300px text-right mt-2">
          <span class="cursor-pointer option-icons" v-if="!showTimeZone" @click="showTimeZone = true"> {{ selected }} </span>
          <model-select v-if="timezones.length > 0 && showTimeZone" :options="timezones" option-value="value" option-text="name" v-model="selected" @input="timeZoneChanged(selected)"> </model-select>
        </div>
      </div>

      <b-row class="px-2 pt-2">
        <!-- Calendar starts here -->
        <b-col class="col-auto pl-2 schedule-calendar">
          <b-calendar v-if="!showCalendar" v-model="selectedDate" @input="getEventSlots" :min="calendarOptions.start" :max="calendarOptions.end" @context="onContext" block locale="en-US"></b-calendar>
        </b-col>
        <!-- Slots starts here -->
        <b-col class="position-relative col px-lg-2 px-0 mx-lg-3 mt-4 mt-lg-0">
          <!-- Generating slots spinner code -->
          <div class="text-center my-4" v-if="!loading">
            <b-spinner variant="primary" label="Spinning" />
            <p class="mb-0 text-gray-600 mt-2">Generating available slots...</p>
          </div>
          <!-- Empty state block -->
          <b-card class="card-inactive rounded-sm" body-class="text-center" v-if="(timesArray.length == 0 || !noSlots) && loading">
            <b-img src="/img/schedulely/schedule.svg" fluid class="w-300px" />
            <div class="mb-3 mt-2">
              <p class="mb-0 font-size-base">No Slots Found</p>
            </div>
          </b-card>
          <!-- Slots found block starts here -->
          <template v-if="!hideSlots && timesArray.length > 0">
            <row ref="button-timeslots" class="max-height-315 timezone-buttons" :class="(slotsCount = 0)">
              <template v-for="(item, index) in timesArray">
                <b-button
                  :key="index"
                  variant="white"
                  class="mr-3 mb-3 slot-button text-nowrap"
                  @click="selectSlots(item, $event)"
                  v-if="
                    (eventResponse[0].date_range_type == 'indefinitely' ||
                      moment
                        .utc(moment(item.start_unix * 1000), 'YYYY-MM-DD HH')
                        .tz(eventResponse[0].timezone)
                        .format('YYYY-MM-DD') <= eventCreatedEndDate) &&
                      moment
                        .utc(moment(item.start_unix * 1000), 'YYYY-MM-DD HH')
                        .tz(deviceTimezone)
                        .format('YYYY-MM-DD') == selectedDate &&
                      moment
                        .utc(moment(item.start_unix * 1000), 'YYYY-MM-DD HH')
                        .tz(eventResponse[0].timezone)
                        .format('YYYY-MM-DD') >= eventCreatedStartDate
                  "
                  v-b-tooltip.hover
                  title="Please select a time slot"
                >
                  <span :class="noSlots ? '' : (noSlots = true)">
                    {{
                      moment
                        .utc(moment(item.start_unix * 1000), "YYYY-MM-DD HH")
                        .tz(deviceTimezone)
                        .format("LT")
                    }}
                  </span>
                  <p class="mb-0" style="line-height: 10px !important;" v-if="eventResponse[0].is_visible_slots && eventResponse[0].category != 'one to one'">
                    <small class="font-size-12px text-primary" v-if="eventResponse[0].slot_limit == item.available_slot_number">{{ item.available_slot_number }} available </small>
                    <small class="font-size-12px text-danger" v-else-if="item.available_slot_number == 1">{{ item.available_slot_number }} available </small>
                    <small class="font-size-12px text-warning" v-else-if="item.available_slot_number < eventResponse[0].slot_limit && item.available_slot_number != 0"
                      >{{ item.available_slot_number }} available
                    </small>
                  </p>
                </b-button>
              </template>
            </row>
          </template>
          <!-- Overaly  -->
          <b-overlay spinner-variant="primary" class="position-unset" opacity="0.5" :show="showLoader" rounded="sm" />
        </b-col>
      </b-row>
    </div>
    <BookSlot
      ref="bookSlotRef"
      :projectId="projectId"
      @showSlots="
        $emit('showUser', 'slot');
        getEventSlots(selectedDate);
      "
      v-if="!isUserData"
      :eventData="eventInfo"
      :userData="userData"
    />
  </div>
</template>

<script>
  import moment from "moment";
  import momentTZ from "moment-timezone";
  import Schedulely from "@/js/schedulely/schedulely";
  import BookSlot from "./BookSlot";
  import { ModelSelect } from "vue-search-select";
  import "vue-search-select/dist/VueSearchSelect.css";
  import { bus } from "@/js/core/emit-bus";

  export default {
    name: "UserPicker",
    components: {
      BookSlot,
      ModelSelect,
    },
    props: {
      userData: Object,
      eventData: Object,
      isSlots: Boolean,
      projectId: Number,
    },
    data() {
      return {
        calendarOptions: {
          min: "",
          max: "",
          selected: "",
        },
        noSlots: false,
        domainId: "",
        timesArray: [],
        copyTimesArray: [],
        selected: momentTZ.tz.guess(),
        timezones: [],
        deviceTimezone: momentTZ.tz.guess(),
        loading: true,
        hideSlots: true,
        showCalendar: true,
        eventResponse: [],
        eventMinTime: null,
        selectedDate: null,
        nextDaySlots: [],
        isUserData: true,
        eventInfo: {},
        incrementTimeSlot: [],
        eventCreatedEndDate: null,
        isUserDetails: false,
        eventCreatedStartDate: null,
        eventBookingCount: null,
        slotsCount: 0,
        showTimeZone: false,
        showLoader: false,
      };
    },
    async mounted() {
      this.showTimeZone = false;
      // Get event booking count
      this.eventBookingCount = await Schedulely.get(`${this.eventData.domain_id}/${this.eventData.created_by}/${this.eventData.id}/events-count`);
      // Get Domain Id
      this.domainId = window.domain;
      // Getting list of time zones
      let timezones = momentTZ.tz.names();
      timezones.map((item) => {
        this.timezones.push({
          text: "(GMT " + moment.tz(item).format("Z") + ") " + item.replace("_", " "),
          value: item,
        });
      });
      // Getting event data
      await this.getEventData(this.eventData.id);
      await this.getEventSlots(this.calendarOptions.start);
    },
    methods: {
      async getEventData(eventId) {
        //document.getElementById("user-picker").scrollIntoView();
        this.$refs["user-picker"].scrollIntoView();
        this.eventResponse = await Schedulely.get(`${this.domainId}/${this.userData.id}/${eventId}`);
        await this.getEventDatails();
      },
      async getEventDatails() {
        this.showCalendar = true;
        var data = this.eventResponse;
        this.eventInfo = {};
        this.eventInfo.eventDetails = data;
        this.showCalendar = false;
        this.eventMinTime = data[0].event_min_time;
        this.start_suspend_date = data[0].start_Suspend_date;
        this.end_suspend_date = data[0].end_suspend_date;
        await this.generateDateRange(data);
        await this.removePastTimes();
      },
      // To get list of available slots
      async getEventSlots(data) {
        this.loading = false;
        this.hideSlots = true;
        this.isUserData = true;

        var startDateEpoch = moment(this.getOffSetValue(data, this.eventResponse[0].timezone, "daterange"))
          .tz(this.eventResponse[0].timezone)
          .startOf("day")
          .unix();
        var endDateEpoch = moment(this.getOffSetValue(data, this.eventResponse[0].timezone, "daterange"))
          .tz(this.eventResponse[0].timezone)
          .endOf("day")
          .unix();
        this.selectedDate = data;
        this.eventInfo.startDateEpoch = startDateEpoch;
        this.eventInfo.endDateEpoch = endDateEpoch;
        let max_limit;
        if (this.eventResponse[0].category == "one to one") max_limit = 1;
        else max_limit = this.eventResponse[0].slot_limit;

        let time = "backward";
        parseInt(
          moment()
            .tz(this.eventResponse[0].timezone)
            .utcOffset(),
        ) > 0
          ? (time = "backward")
          : (time = "forward");
        let response = await Schedulely.get(
          `${this.domainId}/${this.userData.id}/${this.eventData.id}/${startDateEpoch}/${endDateEpoch}/slots?max_limit=${max_limit}&type=${this.eventData.category}&time=${time}&project_id=${this.projectId}`,
        );
        this.copyTimesArray = response;
        // Validating whether the selected date is lesser than the start_date
        if (this.eventResponse && this.eventResponse[0].date_range_type) {
          await this.setupEndDate(this.eventResponse[0]);
        }
        this.loading = true;
        this.hideSlots = false;
        this.isUserDetails = false;
      },
      // Slot Select
      selectSlots(time, event) {
        let a = this.$refs["button-timeslots"].getElementsByClassName("active");
        a.forEach((element) => {
          element.classList.remove("active");
        });
        event.currentTarget.classList.add("active");
        this.isUserData = false;
        // Assigning user selected time to event data
        this.eventInfo.time = time;
        this.eventInfo.deviceTimezone = this.deviceTimezone;
        this.$emit("userDetails");
        this.isUserDetails = true;
      },
      // Time zone changed
      async timeZoneChanged(timeZone) {
        this.timesArray = [];
        this.loading = false;
        this.deviceTimezone = timeZone;
        await this.getEventDatails(this.selectedDate);
        this.loading = true;
      },
      // Removing past times
      removePastTimes() {
        this.showLoader = true;
        this.nextDaySlots = [];
        this.timesArray = [];
        var currentTime = null;
        currentTime = new Date(
          moment
            .utc(moment(), "YYYY-MM-DD HH:mm")
            .tz(this.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        ).getTime();
        let currentDate = moment()
          .tz(this.deviceTimezone)
          .format("YYYY-MM-DD");
        let selectedDate = moment(this.selectedDate).format("YYYY-MM-DD");
        var filteredSlots = [];
        this.copyTimesArray.filter((item) => {
          var time1 = item.start_unix * 1000 - this.eventMinTime * 60000;
          var time = new Date(
            moment
              .utc(moment(time1), "YYYY-MM-DD HH:mm")
              .tz(this.deviceTimezone)
              .format("YYYY-MM-DD HH:mm"),
          ).getTime();
          let date = moment
            .utc(moment(time1), "YYYY-MM-DD")
            .tz(this.deviceTimezone)
            .format("YYYY-MM-DD");
          let min_date = this.calendarOptions.start;
          let max_date = this.calendarOptions.end;
          if (date == selectedDate && (date != currentDate || time >= currentTime)) {
            if (selectedDate == min_date || selectedDate == max_date) {
              // Pushing slot should be in between of event start time and end time
              if (
                moment
                  .utc(moment(time1), "YYYY-MM-DD")
                  .tz(this.eventResponse[0].timezone)
                  .format("YYYY-MM-DD") >= this.eventCreatedStartDate &&
                moment
                  .utc(moment(time1), "YYYY-MM-DD")
                  .tz(this.eventResponse[0].timezone)
                  .format("YYYY-MM-DD") <= this.eventCreatedEndDate
              )
                filteredSlots.push(item);
              this.nextDaySlots.push(item);
            } else {
              filteredSlots.push(item);
              this.nextDaySlots.push(item);
            }
          } else if (date > selectedDate) {
            this.nextDaySlots.push(item);
          }
        });
        this.incrementTimeSlot = filteredSlots;
        // If event type is equals to one to one then generate final slots
        if (this.eventResponse[0].category == "one to one") return this.filterTimeIncrements(this.nextDaySlots);

        var bookedGroupEvents = [];
        // Number of slots count for an hour
        let slot_count = parseInt(this.eventResponse[0].duration) / parseInt(this.eventResponse[0].increment_slots);
        if (slot_count) {
          this.nextDaySlots.filter((slot, index) => {
            if (this.eventResponse[0].slot_limit != slot.available_slot_number) {
              bookedGroupEvents.push(slot);
              this.nextDaySlots.splice(index + 1, slot_count - 1);
            }
          });
        }
        this.filterTimeIncrements(this.nextDaySlots, bookedGroupEvents);
      },
      filterTimeIncrements(data, bookedGroupSlots) {
        let finalSlots = [];
        if (bookedGroupSlots && bookedGroupSlots.length) finalSlots = bookedGroupSlots;
        let min_notice = new Date(
          moment()
            .tz(this.deviceTimezone)
            .format("YYYY-MM-DD HH:mm"),
        );
        // Filtering the slots based on event min notice
        let event_min_notice = JSON.parse(this.eventResponse[0].event_min_notice);
        if (event_min_notice && parseInt(event_min_notice.dataofmin_notice[1]) > 0) {
          if (event_min_notice.dataofmin_notice[0] == "days") min_notice.setDate(min_notice.getDate() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "hours") min_notice.setHours(min_notice.getHours() + parseInt(event_min_notice.dataofmin_notice[1]));
          else if (event_min_notice.dataofmin_notice[0] == "minutes") {
            min_notice.setMinutes(min_notice.getMinutes() + parseInt(event_min_notice.dataofmin_notice[1]));
          }
        }
        data.map((item, index) => {
          let time1 = item.start_unix * 1000 - this.eventMinTime * 60000;
          let currentSlot = new Date(
            moment
              .utc(moment(time1), "YYYY-MM-DD HH:mm")
              .tz(this.deviceTimezone)
              .format("YYYY-MM-DD HH:mm"),
          );
          if (!this.eventResponse[0].increment_slots || this.eventResponse[0].increment_slots == undefined || this.eventResponse[0].increment_slots == null) this.eventResponse[0].increment_slots = 5;
          if (min_notice.getTime() < currentSlot.getTime()) {
            let remainder = parseInt(this.eventResponse[0].duration) % parseInt(this.eventResponse[0].increment_slots);
            if (remainder == "0") {
              currentSlot.setMinutes(currentSlot.getMinutes() + parseInt(this.eventResponse[0].duration));
            } else {
              remainder = parseInt(this.eventResponse[0].increment_slots) - remainder;
              currentSlot.setMinutes(currentSlot.getMinutes() + parseInt(this.eventResponse[0].duration + remainder));
            }
            this.nextDaySlots.map((next) => {
              let time2 = next.start_unix * 1000 - this.eventMinTime * 60000;
              let nextSlot = new Date(
                moment
                  .utc(moment(time2), "YYYY-MM-DD HH:mm")
                  .tz(this.deviceTimezone)
                  .format("YYYY-MM-DD HH:mm"),
              ).getTime();
              if (currentSlot.getTime() == nextSlot) {
                let slot = item.start_unix * 1000 - this.eventMinTime * 60000;
                // Current slot of global array
                slot = new Date(
                  moment
                    .utc(moment(slot), "YYYY-MM-DD HH:mm")
                    .tz(this.deviceTimezone)
                    .format("YYYY-MM-DD HH:mm"),
                );
                slot.setMinutes(slot.getMinutes() + parseInt(this.eventResponse[0].increment_slots));
                // next immediate slot of Current slot in global array
                if (data && data[index + 1] && data[index + 1].start_unix) {
                  let globalNext = data[index + 1].start_unix;
                  globalNext = globalNext * 1000 - this.eventMinTime * 60000;
                  globalNext = new Date(
                    moment
                      .utc(moment(globalNext), "YYYY-MM-DD HH:mm")
                      .tz(this.deviceTimezone)
                      .format("YYYY-MM-DD HH:mm"),
                  ).getTime();
                  // Comparing current slot with the next immediate slot to enable the time
                  if (slot.getTime() == globalNext) finalSlots.push(item);
                }
              }
            });
          }
        });
        this.showLoader = false;
        if (bookedGroupSlots && bookedGroupSlots.length) finalSlots.sort((first_slot, second_slot) => first_slot.start_unix - second_slot.start_unix);
        this.noSlots = false;
        this.timesArray = finalSlots;
        bus.$emit("updateCustomClass");
      },
      getOffSetValue(date, timeZone, type) {
        let localTime = new Date(date);
        let localDate = moment(localTime)
          .utcOffset(0)
          .format("YYYY-MM-DD HH:mm");
        let end = new Date(date);
        end.setMinutes(
          end.getMinutes() +
            moment()
              .tz(timeZone)
              .utcOffset() -
            moment().utcOffset(),
        );
        if (type == "daterange" || this.eventResponse[0].timezone == this.deviceTimezone) return localDate;
        else return end;
      },
      generateDateRange(data) {
        this.showLoader = true;
        this.eventCreatedStartDate = moment(this.getOffSetValue(data[0].start_date, this.eventResponse[0].timezone, "daterange")).format("YYYY-MM-DD");
        this.calendarOptions.start = moment(this.getOffSetValue(data[0].start_date, this.deviceTimezone)).format("YYYY-MM-DD");
        this.calendarOptions.end = null;
        // Getting current date based on time zone
        var now = moment
          .utc(moment(new Date().getTime()), "YYYY-MM-DD HH")
          .tz(this.deviceTimezone)
          .format("YYYY-MM-DD");
        if (now > this.calendarOptions.start) {
          this.calendarOptions.start = now;
          this.eventCreatedStartDate = moment()
            .tz(this.eventResponse[0].timezone)
            .format("YYYY-MM-DD");
        }
        if (data[0].date_range_type == "into_future") {
          this.selectedDate = now;
          this.setupEndDate(data[0], "endDateCal");
        } else if (data[0].date_range_type == "date_range") {
          this.eventCreatedEndDate = moment(this.getOffSetValue(data[0].end_date, this.eventResponse[0].timezone, "daterange")).format("YYYY-MM-DD");
          if (this.eventResponse[0].timezone == this.deviceTimezone) {
            this.calendarOptions.end = this.eventCreatedEndDate;
          } else {
            let end = new Date(data[0].end_date);
            end.setDate(end.getDate() + 1);
            end.setMinutes(
              end.getMinutes() +
                moment()
                  .tz(this.deviceTimezone)
                  .utcOffset() -
                moment().utcOffset(),
            );
            this.calendarOptions.end = moment(end).format("YYYY-MM-DD");
          }
        }
        this.showLoader = false;
      },
      setupEndDate(data, type) {
        this.timesArray = [];
        if (data.date_range_type == "date_range") {
          this.removePastTimes();
        }
        // Providing slots to the next mentioned days/weeks/months
        else if (data.date_range_type == "into_future") {
          let futureDate;
          if (new Date().getTime() > new Date(this.calendarOptions.start).getTime()) futureDate = new Date();
          else futureDate = new Date(this.calendarOptions.start);
          let visibility = JSON.parse(data.visibility);
          if (visibility.dataofvisibility[0] == "days") futureDate.setDate(futureDate.getDate() + parseInt(visibility.dataofvisibility[1]) + 1);
          else if (visibility.dataofvisibility[0] == "weeks") futureDate.setDate(futureDate.getDate() + 1 + parseInt(visibility.dataofvisibility[1]) * 7);
          else if (visibility.dataofvisibility[0] == "months")
            futureDate = new Date(futureDate.getFullYear(), futureDate.getMonth() + parseInt(visibility.dataofvisibility[1]), futureDate.getDate() + 1);
          if (type == "endDateCal") {
            if (futureDate.getTime() <= new Date(this.selectedDate).getTime()) {
              this.eventCreatedStartDate = moment(this.getOffSetValue(data.start_date, this.eventResponse[0].timezone, "daterange")).format("YYYY-MM-DD");
              this.calendarOptions.start = moment(this.getOffSetValue(data.start_date, this.deviceTimezone)).format("YYYY-MM-DD");
            }
            this.eventCreatedEndDate = moment(this.getOffSetValue(futureDate, this.eventResponse[0].timezone, "daterange")).format("YYYY-MM-DD");
            if (this.eventResponse[0].timezone == this.deviceTimezone) {
              this.calendarOptions.end = this.eventCreatedEndDate;
            } else {
              let end = new Date(futureDate);
              end.setDate(end.getDate() + 1);
              end.setMinutes(
                end.getMinutes() +
                  moment()
                    .tz(this.deviceTimezone)
                    .utcOffset(),
              );
              this.calendarOptions.end = moment(end).format("YYYY-MM-DD");
            }
          }
          this.removePastTimes();
        }
        // Keeping all slots in open
        else if (data.date_range_type == "indefinitely") {
          this.eventCreatedEndDate = null;
          this.removePastTimes();
        }
      },
      // Deafult method of bootstrap calendar
      onContext(ctx) {
        this.context = ctx;
      },
      showBooking(type) {
        if (type == "booking" && this.eventInfo && this.eventInfo.time) {
          this.isUserData = false;
          this.isUserDetails = true;
          this.$emit("userDetails");
        }
      },
    },
  };
</script>

<style>
  @import url("/css/schedule/theme.css?v=infinity_release_tag");
  .line-height-15 {
    line-height: 1.5;
  }
  .tooltip.b-tooltip {
    display: block !important;
    opacity: 0.9;
    outline: 0;
  }
  .tooltip.b-tooltip {
    pointer-events: none;
  }
</style>
